.Cover {
  -ms-flex-line-pack: center;
      align-content: center;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  height: 100%;
  -ms-flex-pack: center;
      justify-content: center;
}

.Cover img {
  border: 2px solid #929292;
  -webkit-box-shadow: 0 19px 38px rgba(0, 0, 0, 0.3), 0 15px 12px rgba(0, 0, 0, 0.22), 0px 20px 90px 20px rgba(50, 50, 50, 0.8);
          box-shadow: 0 19px 38px rgba(0, 0, 0, 0.3), 0 15px 12px rgba(0, 0, 0, 0.22), 0px 20px 90px 20px rgba(50, 50, 50, 0.8);
  margin: 0 auto;
}

@media (min-width: 992px) {
  .Cover img {
    max-width: 155px;
  }
}



